body {
  font-family: sans-serif;
}

:root {
  /* --purple
  --silver
  --light-purple
  --gray */
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-padding-top: 7rem;
  scroll-behavior: smooth;
}

* {
  /*   font-family: */
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: all 0.2s linear;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 3rem 9%; /* change these values */
}

.header .nav a {
  font-size: 1.6rem;
  margin: 1rem;
  color: black;
}

.header .nav a:hover {
  /* color: purple; */
}

.btn {
  display: inline-block;
  margin-top: 1rem;
  border-radius: 0.5rem;
  /*   background: purple;
  color: black;  change these values */
  font-weight: 600;
  font-size: 1.6rem;
  cursor: pointer;
  padding: 0.8rem 3rem;
}

.btn:hover {
  /* background: */
}

/* responsive design */

@media (max-width: 1024px) {
  :root {
    font-size: 55%;
  }
  .header {
    padding: 2rem;
  }
}

@media (max-width: 768px) {
  :root {
    font-size: 55%;
  }
  .header .nav {
    position: absolute;
    top: 99%; /* shift the navbar right below the header when resolution is shrunk */
    left: 0;
    right: 0;
    background: white;
    border-top: 0.1rem solid rgba(0, 0, 0, 0.1);
  }
}

@media (max-width: 480px) {
  :root {
    font-size: 50%;
  }
}
